import React from "react";
import { NavLink, Switch, Route, Redirect } from "react-router-dom";
import Typical from "react-typical";
import "./index.css";
import Home from "./Home";
import About from "./About";
import Project from "./Project";
// import Contact from "./Contact";
import Footer from "./Footer";

const App = () => {
    return(
        <>

<section className="s1">
		<div className="main-container">
			<div className="greeting-wrapper">
				<h1><span style={{color:"white" , fontFamily: 'Russo One'}}>Hi, I'm {" "}</span>
                <Typical  
                  loop={Infinity}
                  wrapper="b"
                  steps={["Developer", 2500, "Designer", 2500, "Shubham Garg", 2500]}
                ></Typical>
                </h1>
			</div>


			<div className="intro-wrapper">
				<div className="nav-wrapper">

					{/* <!-- Link around dots-wrapper added after tutorial video --> */}
					<a href="index.html">
						<div className="dots-wrapper">
							<div >
                                <span style={{fontWeight:1000 , fontSize:"30px"}}>Shubham<span style={{color:"#e5ff00"}}>Garg</span></span>
                            </div>
							{/* <div id="dot-2" className="browser-dot"></div>
							<div id="dot-3" className="browser-dot"></div> */}
						</div>
					</a>
					

					<ul id="navigation" style={{fontWeight:1000 , fontSize:"30px"}}>
						<li><NavLink to="/" className="aa">Home</NavLink></li>
                        <li><NavLink to="/about" className="aa">About</NavLink></li>
                        <li><NavLink to="/project" className="aa">Projects</NavLink></li>
                        {/* <li><NavLink to="/contact" className="aa">Contact</NavLink></li> */}

					</ul>
				</div>
			</div>
		</div>
	</section>

    <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/about" component={About} />
        {/* <Route exact path="/service" component={Service} /> */}
        {/* <Route exact path="/skill" component={Skill} /> */}
        <Route exact path="/project" component={Project} />
        {/* <Route exact path="/contact" component={Contact} /> */}
        {/* <Route exact path="/side" component={SideMenu} /> */}
        <Redirect to="/" />
    </Switch>

    <Footer/>
    </>
    );
}

export default App;